.chart {
  position: relative;
  width: 300px;
  height: 300px;
  border-radius: 50%;
  transition: 0.3s;
  background: lightgray;
  display: inline-block;
}

.chart:after {
  /* 가상선택자는 도넛 모양을 만들기 위함이다.*/
  content: "";
  background: #fff; /* 백그라운드 컬러로 중앙가리기 */
  position: absolute;
  top: 50%;
  left: 50%;
  width: 200px;
  height: 200px; /* 도넛의 너비 설정 */
  border-radius: 50%;
  transform: translate(-50%, -50%);
}
.chart-bar {
  width: inherit;
  height: inherit;
  border-radius: 50%;
  background: conic-gradient(
    red 36deg,
    orange 36deg 170deg,
    yellow 170deg
  ); /* 차트 설정 */
}

.piechart {
  position: relative;
