/* 여기는 프로젝트 */

.project {
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  position: relative;
  height: 300px;
  width: 100%;
  display: block;
  margin-bottom: 30px;
}

.project .desc .con {
  padding: 20px;
  display: block;
  position: relative;
  height: 300px;
}

.project img {
  padding: 20px;
  display: block;
  position: relative;
  width: 100%;
  height: 300px;
}

.project:hover .desc .con span {
  transform: translate3d(0, 0, 0);
}

.project .desc {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #2c98f0;
  opacity: 0;
  transition: 1s;
}

.project:hover .desc {
  opacity: 1;
}

.project .desc .con span {
  display: block;
  color: rgba(255, 255, 255, 0.7);
  font-size: 13px;
  transition: opacity 0.3s, -webkit-transform 0.3s;
  transition: transform 0.3s, opacity 0.3s;
  transition: transform 0.3s, opacity 0.3s, -webkit-transform 0.3s;
  transform: translate3d(0, 15px, 0);
}

.project .desc h3 a {
  color: #fff;
}

.project .desc .con .icon {
  position: absolute;
  bottom: 20px;
  left: 20px;
  right: 20px;
}

.project .desc .con .icon span {
  display: inline;
  padding-right: 5px;
}

.project .desc .con .icon span a {
  color: #333333;
  padding: 5px;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 5px;
}

a {
  color: #2c98f0;
